section {
  width: 100%;

  .about {
    margin-top: 100px;
    .about_text {
      display: flex;
      align-items: center;
      .md-block({
       flex-direction: column;
       justify-content: center;
      });
      .img_logo {
        width: 40%;
        .md-block({
           
          width: 100%;
          text-align: center;
          margin-bottom: 20px;
        });
      }
      .text {
        width: 60%;
        .md(width , 100%);
        .lg(margin-left , 20px);
        p {
          font-size: 14px;
          font-weight: 400;
          line-height: 30px;
          text-align: justify;
        }
      }
    }
  }

  .benefits {
    .text {
      text-align: center;
      margin: 50px 0px;
      .sm-block({
        margin : 20px 0px;
      });
      .img-icon {
        max-width: 100px;
        height: 100px;
        margin: auto;
      }
      h3 {
        font-size: 22px;
        font-weight: 700;
        margin: 20px 0px;
        text-align: left;
        font-family: "etelkBold";
        color: #00467a;
        &::after {
          content: "";
          display: block;
          margin-top: 10px;
          width: 20%;
          height: 4px;
          background: #00467a;
        }
      }
      p {
        font-family: "etelkLight";
        font-size: 14px;
        font-weight: 100;
        line-height: 20px;
        text-align: left;
        .sm(text-align , justify);
      }
    }
  }
  .contact {
    margin-top: 100px;
    .map {
      margin-top: 50px;
      margin-bottom: 50px;
    }
    .contact-info {
      text-align: center;
      h3 {
        font-size: 24px;
        font-weight: 700;
        font-family: "etelkBold";
        margin-bottom: 20px;
        text-align: center;
        color: #f49d37;
        .sm-block({
          font-size: 20px;
          margin: 5px;
        });
      }

      p {
        color: #000;
        font-size: 18px;
        font-family: "etelkLight";
        font-weight: 700;
        text-align: left;
        .sm-block({
          text-align: center;
        });
        .md(font-size , 16px);
        .md(text-align , center);
      }
      a {
        text-decoration: none;
        font-size: 18px;
        font-family: "etelkLight";
        font-weight: 1700;
        text-align: center;
        display: inline-block;
        color: #000;
        .md(font-size , 16px);
        &:hover {
          transition: all 500ms;
          color: #f49d37;
          transform: scale(1.1);
        }
      }
      .instagram {
        margin: 0px 15px;
      }
    }
    .contact {
      margin-top: 40px;
    }
  }

  .cardProduct {
    position: relative;
    margin-top: 100px;
    .sm(margin-top , 0px);
    .card-product {
      width: 100%;
      border: 2px solid #d1d1d1;
      margin: 20px 0px;
      position: relative;
      .card-product-img {
        width: 100%;
        height: 200px;
        background: #d1d1d1;
        position: relative;
        img {
          width: 100%;
          height: 200px;
          border-bottom: 2px solid #d1d1d1;
        }
        .card-product-icon {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          display: flex;
          justify-content: space-between;
          a {
            padding: 10px;
            background: #00467a;
            color: white;
            font-size: 16px;
            font-weight: 700;
            transition: 500ms;
            &:hover {
              color: #f49d37;
            }
          }
        }
      }
      .card-product-text {
        padding: 15px;
        .text-title {
          font-size: 15px;
          font-weight: 700;
          text-transform: capitalize;
          margin: 10px 0px;
          line-height: 20px;
          display: block;
          text-decoration: none;
        }

        .price {
          font-size: 18px;
          font-weight: 500;
          margin-bottom: 10px;
          text-align: center;

          padding: 10px 0px;
          .fas {
            color: #00467a;
            margin-right: 10px;
          }
        }
        .card-helper-btn {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 10px -15px;
          border-top: 2px solid #d1d1d1;
          padding: 10px 5px;
          p {
            font-size: 13px;
            font-weight: 700;
          }
          .date {
            .far {
              font-size: 14px;
              font-weight: 700;
              color: #00467a;
            }
          }
          .card-category {
            border-left: 2px solid #d1d1d1;
            border-right: 2px solid #d1d1d1;
            padding: 0px 5px;
          }
          .card-view {
            font-size: 14px;
            text-decoration: none;
            color: black;
            font-weight: 700;
            transition: 500ms;
            &:hover {
              color: #00467a;
            }
          }
        }
        .update {
          font-size: 14px;
          font-weight: 500;
          display: block;
          text-align: center;
          text-decoration: none;
        }
      }
    }

    .card-product-active {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(1, 1, 12, 0.5);
      box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
      backdrop-filter: blur(20px);
      border: 1px solid rgba(255, 255, 255, 0.18);
      display: flex;
      justify-content: center;
      align-items: center;
      color: white !important;
      font-size: 30px;
      font-weight: 700;
    }
  }
  .productInfo {
    margin-top: 100px;
    .sm(margin-top , 0px);
    .infoCard {
      border: 1px solid #adadad;
      .infoCard-img {
        width: 100%;
        height: 500px;
        background: #d1d1d1;
        img {
          max-height: 500px;
        }
      }
      .infoCard-text {
        padding: 15px;
        h3 {
          font-size: 24px;
          font-weight: 700;
          margin-bottom: 25px;
          letter-spacing: 2px;
          line-height: 28px;
        }
        .price {
          font-size: 18px;
          font-weight: 500;
          margin-bottom: 10px;
          text-align: center;
          padding: 10px 0px;
          .fas {
            color: #00467a;
            margin-right: 10px;
          }
        }
      }
      .infoCard-text-helper {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin: 10px 0px;
        border-top: 2px solid #d1d1d1;
        padding: 10px 5px;
        p {
          font-size: 16px;
          font-weight: 500;
        }
        .date {
          margin-right: 20px;
          .far {
            font-size: 16px;
            font-weight: 500;
            color: #00467a;
          }
        }
        .card-category {
          span {
            color: #00467a;
          }
          border-left: 2px solid #d1d1d1;
          border-right: 2px solid #d1d1d1;
          padding: 0px 15px;
        }
      }
      .product-text {
        border-top: 2px solid #d1d1d1;
        h4 {
          font-size: 20px;
          font-weight: 700;
          margin: 15px 0px;
          padding: 0px 15px;
          letter-spacing: 2px;
        }
        p {
          font-size: 14px;
          font-weight: 500;
          line-height: 28px;
          padding: 0px 15px;
        }
      }
    }
    .left-info-img{
      width: 100%;
      height: 80%;
      border: 2px solid #d1d1d1;
      position: relative;
      background: #00467a;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .productInfo-carousel{
      h3{
        margin: 40px 0px;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 3px;
      }
      .productInfo-carousel-card{
        width: 100%;
        border: 2px solid #d1d1d1;
        margin: 20px 0px;
        .productInfo-carousel-img{
          width: 100%;
          height: 120px;
          background: #d1d1d1;
          img{
            width: 100%;
            height: 120px;
          }
        }
        h4{
          font-size: 14px;
          line-height: 20px;
        }
        .productInfo-carousel-text{
          padding: 15px;
          .price {
            font-size: 12px;
            font-weight: 500;
            margin-bottom: 10px;
            text-align: center;
            padding: 10px 0px;
            .fas {
              color: #00467a;
              margin-right: 10px;
            }
          }
          .card-helper-btn {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 10px -15px;
            border-top: 2px solid #d1d1d1;
            border-bottom: 2px solid #d1d1d1;
            padding: 10px 5px;
            p {
              font-size: 13px;
              font-weight: 700;
            }
            .date {
              .far {
                font-size: 12px;
                font-weight: 500;
                color: #00467a;
              }
            }
            .card-category {
              border-left: 2px solid #d1d1d1;
              border-right: 2px solid #d1d1d1;
              padding: 0px 5px;
            }
            
          }
          .card-view {
            font-size: 14px;
            text-decoration: none;
            color: black;
            font-weight: 700;
            transition: 500ms;
            text-align: center;
            display: block;
            &:hover {
              color: #00467a;
            }
          }
        }
      }

    }
  }

}

